.container {
	max-width: 120rem;
	margin: 8rem auto;
	background-color: var(--color-grey-light-2);
	box-shadow: var(--shadow-dark);

	min-height: 50rem;
}
.header {
	height: 7rem;
	background-color: #fff;
	border-bottom: 1px solid var(--solid-grey-light-2);
}
.content {
	display: flex;
}

.sidebar {
	background-color: var(--color-grey-dark-1);

	flex: 0 0 18%;
}

.hotel-view {
	background-color: #fff;
	background-color: green;

	flex: 1;
}
